<template>
    <div id="content-wrapper" class="container wrapper">
       <div class="blog-content col-md-8">
           <h5 class="dark-title-with-bord pad_title">Listing posts for category <b>"{{category}}"</b></h5>
            <PostPreviewListItem
                v-for="post in posts"
                :key="post.id"
                :title="post.title"
                :slug="post.slug" 
                :created="post.created"
            />      
        </div>
    </div>
</template>

<script>
import { butter } from "@/plugins/buttercms"
import PostPreviewListItem from "@/components/PostPreviewListItem"

export default {
    name: 'TagPosts',
    components: {
        PostPreviewListItem
    },
    props: ['category'],
    data() {
      return {
        posts: []
      }
  },
  methods: {
      getPostByCategory() {
          butter.category.retrieve(this.category, {include: 'recent_posts'}).then((res) => {
              this.posts = res.data.data.recent_posts.filter((post => {
                 return post.tags.some((tag) => tag.name === 'en') 
              }))
          })
      }
  },
  mounted() {
      this.getPostByCategory()
  }
}
</script>

<style scoped>
.pad_title {
    padding-bottom: 50px;
}
</style>